.footer {
    width: 100%;
    height: 100vh;
    background-color: black;
    color: white;
    padding: top;
    position: fixed;
    z-index: -2;
    bottom: 0;
    left: 0;

}

.footer-info {
    font-size: 35px;
    padding: 100px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.footer-info p {
    color:white;
    padding: 0;
}
.texto {
    padding-top: 100px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

}
.texto > h1 {
    padding-bottom: 15px;
}

.footer-contact {
    font-size: 22px;
    padding: 100px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

.footer-sns {
    width: 70%;
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
}
.sns-links {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
}
.sns-links a {
    color:white;
}

.iconos {
    color: white;
    font-size: 35px ;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;
}

.iconos:hover {
    border-color: white;
    transition: all 0.3s ease-in-out;
}

.design-by {
    font-size: 18px;
    padding-left: 50px;
    color: white;
}

@media  only screen and (max-width:960px) {
    .footer-info {
        text-align: center;
        padding: 0;
        font-size: 20px;
        color: white
    }
    .footer-contact {
        font-size: 16px;
        padding: 100px 0 30px 0;
        text-align: center;
    }
    .footer-sns {
        grid-template-columns: 1fr;
        text-align: center;
    }
    .design-by {
        padding: 0 0 40px 0;
    }
}
